import { Delaunay, NVector3 } from '../../../lib/NJSCore';
import { RendererCanvas } from '../../../lib'

// https://d3js.org/d3-delaunay/delaunay
import { Delaunay as DelaunayD3 } from "d3-delaunay";

const getDelaunay = (points) => {
    const pointsArray = points.map( (p) =>  [p.vec.x, p.vec.y, p.vec.z]);
    return DelaunayD3.from(pointsArray);
}

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '90%';
        div.style.height = '50%';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);



        // console.log(this.renderer);
        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {
    delaunay: Delaunay
    vs: Point[] = [];
    selVec: Point | undefined = undefined;
    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation
        // 

        this.vs = [];
        for (let i = 0; i < 5; ++i) {
            this.vs.push(new Point(Math.random() * this.canvas.width, Math.random() * this.canvas.height));
        }

        // console.log(this.vs)

        this.compute();




        // Example Usage
        const g = new Graph();

        g.addVertex(0);
        g.addVertex(1);
        g.addVertex(2);
        g.addVertex(3);
        g.addVertex(4);

        g.addEdge(0, 1, 2);
        g.addEdge(0, 3, 6);
        g.addEdge(1, 2, 3);
        g.addEdge(1, 3, 8);
        g.addEdge(1, 4, 5);
        g.addEdge(2, 4, 7);
        g.addEdge(3, 4, 9);

        const MST = g.primMST(0);

        console.log("Minimum Spanning Tree:");
        MST.forEach(edge => {
            console.log(`${edge.vertex1} - ${edge.vertex2} : ${edge.weight}`);
        });



        this.start();
    }
    public mouseDown(x: number, y: number): void {
        const mv = new NVector3(x, y)

        for (let i = 0; i < this.vs.length; ++i) {
            const dis = this.vs[i].vec.Distance(mv)
            // console.log(dis)
            if (dis < 6) {
                this.selVec = this.vs[i];
                return;
            }
        }

        // this.selVec = undefined;
    }
    public mouseDrag(x: number, y: number): void {
        // console.log(this.selVec)
        if (this.selVec) {
            this.selVec.vec.x = x;
            this.selVec.vec.y = y;
            this.compute();

        }
    }
    public mouseUp(x: number, y: number): void {
        this.selVec = undefined;
    }
    public mouseClick(x: number, y: number): void {
        this.vs.push(new Point(x, y));
        this.compute();

    }
    public compute() {
        this.delaunay = new Delaunay(this.vs.map((v) => v.vec));
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop

        // this.delaunay = new Delaunay(this.vs);
        this.delaunay.DrawTriClass(ctx)

        this.vs.forEach((v) => {
            v.render(ctx)
        })


        const del = getDelaunay(this.vs)
        console.log(del)

        for (let i = 0 ; i < del.points.length; i+=2) {
            ctx.beginPath()
            ctx.arc(del.points[i], del.points[i+1], 2, 0, Math.PI * 2)
            ctx.closePath()
            ctx.fillStyle = '#ff0';
            ctx.fill()
        }

        const {points, halfedges, triangles} = del;
        for (let i = 0, n = halfedges.length; i < n; ++i) {
            const j = halfedges[i];
            if (j < i) continue;
            const ti = triangles[i];
            const tj = triangles[j];
            ctx.beginPath()
            ctx.moveTo(points[ti * 2], points[ti * 2 + 1]);
            ctx.lineTo(points[tj * 2], points[tj * 2 + 1]);
            ctx.closePath()
            ctx.lineWidth = 3
            ctx.strokeStyle = 'rgba(255, 255, 0, 0.1)'
            ctx.stroke()
        }

        del.render(ctx)

        // console.log(this.vs.length)
        // !! can stop render after this frame.
        // this.isStatic = true;
    }

}


class Point {
    public vec: NVector3
    constructor(x, y, z = 0) {
        this.vec = new NVector3(x, y, z);

    }
    public render(ctx) {
        ctx.fillStyle = '#f00'
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, 6, 0, Math.PI * 2)
        ctx.closePath();
        ctx.fill();
    }
}


class Graph {
    vertices;
    edges;
    constructor() {
        this.vertices = [];
        this.edges = [];
    }
    addVertex(vertex) {
        this.vertices.push(vertex);
    }

    addEdge(vertex1, vertex2, weight) {
        this.edges.push({ vertex1, vertex2, weight });
    }

    primMST(startVertex) {
        const visited = {};
        const MST = [];

        visited[startVertex] = true;

        while (MST.length < this.vertices.length - 1) {
            let minEdge = null;

            this.edges.forEach(edge => {
                if ((visited[edge.vertex1] && !visited[edge.vertex2]) ||
                    (!visited[edge.vertex1] && visited[edge.vertex2])) {
                    if (!minEdge || edge.weight < minEdge.weight) {
                        minEdge = edge;
                    }
                }
            });

            if (!minEdge) {
                break;
            }

            MST.push(minEdge);
            visited[minEdge.vertex1] = true;
            visited[minEdge.vertex2] = true;
        }

        return MST;
    }
}